import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { AlertController, IonItemSliding, Platform } from '@ionic/angular';
import { CatagoryService } from '../Service/catagory.service';
import { Plugins,CameraSource,CameraResultType } from '@capacitor/core';
import { Catagory, Items, Lookup } from 'src/Tabels/tabels-list';
import { LookupService } from '../Service/lookup.service';
import { ItemsService } from '../Service/items.service';
@Component({
  selector: 'app-items',
  templateUrl: './items.page.html',
  styleUrls: ['./items.page.scss'],
})
export class ItemsPage implements OnInit {

  @ViewChild('filePicker', {static: false}) filePickerRef: ElementRef<HTMLInputElement>;
  base64textString: string;
  usePicker = false;
  regform = this.fb.group({});
  itemId: string;
  ListOfCategory: Catagory[];
  filteredCategory: Catagory[];
  listoflookup: Lookup[];
  listofItems: any[];
  
  constructor(private fb : FormBuilder ,private platform: Platform ,private CategoryService: CatagoryService,
   private alertController: AlertController,  private lookupService:LookupService, private itemsService:ItemsService
    ) {
     
     }

  ngOnInit() {
     this.regform =this.fb.group({
      name:['',Validators.required],
      AmaricName:['',Validators.required],
      discrption:[''],
      CatagoryId:['',Validators.required],
      price:['',Validators.required],
      Quantity:['',Validators.required],
      storeid:['',Validators.required],
      brand:[''],
      picture:['',Validators.required],
      remark:['']
    });
    
    this.getCategory();
    this.getItemList();
    if ((this.platform.is('mobile') && !this.platform.is('hybrid')) ||
      this.platform.is('desktop')
   ) {
     this.usePicker = true;
   }
     
    }
      
    getCategory()
    {
      try {
        this.CategoryService.getallCategorys().subscribe(result=>{
         if(result.length>0)
         {
           this.ListOfCategory =result;
           this.filteredCategory=result;
         }
         else
         {
          this.AlertInternet();
         }
         
        }
        );
      }
      catch(error) {
       console.log(error);
      }
    }
    getItemList()
    {
      this.itemsService.getallItem().subscribe(result=>{
      this.listofItems=  result;
      
      });
    }
    onOptionsSelected($event)
    {
         this.lookupService.getlookupBytype($event.target.value).subscribe(result=>
        {
           this.listoflookup=result;
        });
      
    }
    async AlertInternet() {
      const alert = await this.alertController.create({
        cssClass: 'my-custom-class',
        header: 'Internet',
        // subHeader: 'Subtitle',
        message: 'Please turn on wifi or data',
        buttons: ['OK']
      });
  
      await alert.present();
    }
  
    onPickImage() {
      if(this.usePicker==true){
        // if (!Capacitor.isPluginAvailable('Camera')) {
          this.filePickerRef.nativeElement.click();
          return;
        }
      Plugins.Camera.getPhoto({
        quality: 50,
        source: CameraSource.Prompt,
        correctOrientation: true,
        height: 320,
        width: 300,
        resultType: CameraResultType.Base64
      })
        .then(image => {
          this.base64textString='data:image/png;base64,' + image.base64String;
        })
        .catch(error => {
          console.log(error);
          if (this.usePicker) {
            this.filePickerRef.nativeElement.click();
          }
          return false;
        });
    }
    onFileChosen(event: Event) {
      const pickedFile = (event.target as HTMLInputElement).files[0];
      if (!pickedFile) {
        return;
      }
      const fr = new FileReader();
      fr.onload = () => {
        const dataUrl = fr.result.toString();
        this.base64textString = dataUrl;
      };
      fr.readAsDataURL(pickedFile);
    }
    filter(query) { 
     
        this.filteredCategory = (query.target.value) ? this.ListOfCategory.filter(p => p.name.toLowerCase().includes(query.target.value.toLowerCase())) :
        this.ListOfCategory;
    }
    Save()
    {
      this.regform.get('picture').setValue(this.base64textString);
      if(this.regform.valid)
      {
     if(!this.itemId)
     {
       this.itemsService.create(this.regform.value);
     }
     else
     {
      this.itemsService.updateItem(this.regform.value,this.itemId);
     }
      this.presentAlert();
      this.base64textString = '';
      this.regform.reset();
      this.itemId=null;
      this. getCategory();
    }
    else
    {
        this.ErrorAlert();
    }
    }
     
    delete(item :Items,slidingItem: IonItemSliding )
    {
      this.presentAlertConfirm(item)
      slidingItem.close();
    }
    async presentAlertConfirm(item :Items) {
      const alert = await this.alertController.create({
        header: 'Confirm!',
        message: '<strong>are you sure you want delete?</strong>',
        buttons: [
          {
            text: 'Cancel',
            role: 'cancel',
            cssClass: 'secondary',
            handler: (blah) => {
              console.log('Confirm Cancel: blah');
            }
          }, {
            text: 'OK',
            handler: () => {
            this.CategoryService.removeCategory(item.id);
           this.regform.reset();
           this.itemId=null;
            }
          }
        ]
      });
  
      await alert.present();
    }
    async ErrorAlert() {
      const alert = await this.alertController.create({
        cssClass: 'my-custom-class',
        header: 'Error',
        // subHeader: 'Subtitle',
        message: 'Pleas Enter All field.',
        buttons: ['OK']
      });
  
      await alert.present();
    }
    async presentAlert() {
      const alert = await this.alertController.create({
        cssClass: 'my-custom-class',
        header: 'Catagory',
        // subHeader: 'Subtitle',
        message: 'Catagory Saved successfully.',
        buttons: ['OK']
      });
  
      await alert.present();
    }
     
    Edit(item :Items,slidingItem: IonItemSliding )
    {
        this.itemId =item.id;
        this.regform.get('name').setValue(item.name);
        this.regform.get('AmaricName').setValue(item.AmaricName);
        this.regform.get('discrption').setValue(item.discrption);
        this.regform.get('CatagoryId').setValue(item.CatagoryId);
        this.regform.get('price').setValue(item.price);
        this.regform.get('Quantity').setValue(item.Quantity);
        this.regform.get('storeid').setValue(item.storeid);
        this.regform.get('brand').setValue(item.brand);
        this.regform.get('remark').setValue(item.remark);
        this.base64textString = item.picture;
        slidingItem.close();
    }
    // async ShowModal()
    // {
     
    //   const modal = await this.modalCtrl.create({

    //    component: LoginPage
    //    //componentProps: 
    //  });
  
    //  return await modal.present();
     
    // }

    // ShowLogin()
    // {
    //   this.ShowModal();
    // }
    // Logout()
    // {
    //   this.loginservice.LoginUser.next(false);
    //   this.ISLogin=false;
    //   this.navMod.navigateBack(['/tab']);
    // }
  }
  