import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/internal/operators/map';
import { Items } from 'src/Tabels/tabels-list';

@Injectable({
  providedIn: 'root'
})
export class ItemsService {
  private ItemCollection: AngularFirestoreCollection<any>;
  private Items: Observable<any[]>;
  constructor(db: AngularFirestore) { 
   this.ItemCollection = db.collection<Items>('Items');
 
  }

 create(Category) { 
  return this.ItemCollection.add(Category);
  //return this.db.list('/Category').push(Category);
}
getallItem() {
   
  this.Items = this.ItemCollection.snapshotChanges().pipe(
    map(actions => {
      return actions.map(a => {
        const data = a.payload.doc.data();
        const id = a.payload.doc.id;
        return { id, ...data };
      });
    })
  );
  return this.Items;
}
getItem(id) {
  return this.ItemCollection.doc<any>(id).valueChanges();
}

updateItem(Item: Items, id: string) {
  return this.ItemCollection.doc(id).update(Item);
}

addItem(Item: any) {
  return this.ItemCollection.add(Item);
}

removeCategory(id) {
  return this.ItemCollection.doc(id).delete();
}
}

